@media screen and (max-width:760px) {
    header {
        background-color:rgba(0,0,0,0.75);
        padding:1rem;
        padding: 0 !important;
        position: relative;

        //top: 0;
        z-index: 2;
    
        nav > ul {display: none;}

        .mobile-nav {
            padding:1rem 2rem;
            display: flex;
            justify-content: space-between;
            align-items: stretch;
            width: 100%;
            overflow: hidden; 
            position:relative;
            box-sizing: border-box;
            a {padding:0;}
            .logo {
                padding-bottom:0;
                max-height: 120px;
                max-width: 50vw;
            }
            .mobile-menu-items {
                position:absolute;
                top: 0;
                bottom: 0;
                right: -100%;
                transition: right 200ms;
                width: 100%;
                background-color:rgba(0,0,0,0.75);

                //min-width:50vw;
                &.show { right:0; }

                a {cursor: pointer;}

                ul {
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    align-items: center;
                    // padding-right: calc( #{$hamburger-layer-width} + 2.5rem );
                    padding-top: 1rem;
                    padding-bottom: 1rem;
                    box-sizing:border-box;
                }
                li { 
                    &:hover { 
                        a {color: $purple;}
                    }
                }

            }
            
        }

    }
    .shadow {
        display: none;
        top:0;
    }
}
@media screen and (max-width:360px) {
    header .mobile-nav {
        padding:1rem;
    }
    #menu-toggle.is-active {
        transform-origin: bottom right;
        transform: scale(0.5);

    }
}


